                        </div>
                    </div>
                    <video src="" id='videoId' controls class='videoId'></video>
                    <div class="whiteBox" v-if="!configData.requireCopyRight">
                        <div class="whiteBoxTit">From movie</div>
                        <div class="movieSlectBox" @click="showSearch()">
                            <div class="movieSelectName van-ellipsis" :style="copyRightName ? 'color: #173143;':''">{{copyRightName ? copyRightName : 'Choose a movie or TV episode'}}</div>
                            <img class='blueRightArrow' src="../assets/images/blueRightArrow.png" alt="">
                        </div>
                    </div>
                    <div class="whiteBox">
                        <div class="whiteBoxTit">Genres</div>
                        <div class="genresListBox">
                            <div class="genresItem" :class='item.isSelect ? "genresActive":""' v-for="(item,index) in genresData" :key="index" @click="selectGenres(item,index)">{{item.name}}</div>
                        </div>
                        <div class="whiteBoxTit">Intro</div>
                        <div class="textareaBox">
                            <textarea class='IntroInput' :maxlength="100" name="" id="" v-model="intro" @input="inputIntro(intro)"></textarea>
                            <div class="introAlertBox flexBetween">
                                <span class='errorText'>{{IntroAlertText}}</span>
