                        </div>
                        <div class="submitBtn " :class="checkPosted ? 'GradientAllowBtn':''" style="margin-top: .45rem;margin-bottom: .2rem;" @click="postVideo()">Post</div>
                    </div>
                </div>
            </template>
            <template v-else>
                <div class="orderDetailWrap">
                    <div class="creatSuccessBox">
                        <div class="creatSuccessImgBox">
                            <van-image
                                class='creatSuccessImg'
