                        </div>
                        <div class="creatSuccessTitle">Your video has been posted<br>Please wait for review</div>
                        <div class="creatSuccessScript">Note: Non-original and infringing <br>content may be rejected</div>
                        <div class="creatBotBox">
                            <div class="creatBotTit">This video will be published to<br>any platform after approval</div>
                            <div class="flexBetween" style="padding:0 .68rem;margin-bottom: .24rem;">
                                <img style="width:.43rem;height: .46rem;" src="../assets/images/faceBook1.png" alt="">
                                <img style="width:.57rem;height: .43rem;" src="../assets/images/youtube1.png" alt="">
