                                <img style="width:.46rem;height: .47rem;" src="../assets/images/xiaolian1.png" alt="">
                                <img style="width:.46rem;height: .47rem;" src="../assets/images/chongzi1.png" alt="">
                                <img style="width:.91rem;height: .49rem;" src="../assets/images/vosh1.png" alt="">
                                <img style="width: 1rem;height: .40rem;" src="../assets/images/ted1.png" alt="">
                            </div>
                        </div>
                        <div class="submitBtn blueAllowBtn" style="width: 100%;margin-top: .45rem;margin-bottom: .2rem;" @click="goBack()">Confirm</div>
                    </div>
                </div>
            </template>
        </template>
        <template v-else>
            <div class="navBarBox searchHeader flexBetween" ref="navBarHeight">
                <div class="searchInputBox">
                    <input class='searchInput' placeholder="Search in the copyright library" ref='searchInput' type="text" v-model="searchWord" @focus="showResult = false">
