                </div>
                <p class='searchCancelText' @click="showSearchBox = false">Cancel</p>
            </div>
            <div class="">
                <div class="searchChartBox" v-if="!showResult && searchWord.length >= 3">
                    <div class="searchChartItem" @click="clickSearch()">
                        <img class='searchIcon' src="../assets/images/searchIcon.png" alt="">
                        <p class='searchChart'>
                            Search:
                            <span style="color:#38ADFF;">{{searchWord}}</span>
                        </p>
                    </div>
                </div>
                <div class="searchResultBox" v-else-if="showResult">
                    <template v-if="searchList.length > 0">
                        <van-list
                            v-model="listLoading"
                            :finished="finished"
                            loading-text='Loading...'
                            @load="getSearchData()"
                            :immediate-check='false'
                        >
                            <div class="resultItem flexBetween" v-for="(item,index) in searchList" :key="index" @click="selectMovie(item)">
                                <div class="resultMain flexStart">
                                    <!-- <div class="resultImgBox">
                                        <img class='searchResultImg' :src="item.coverUrl" alt="">
                                    </div> -->
                                    <van-image
                                        class='resultImgBox'
                                        :src="item.coverUrl"
                                        fit="contain"
                                    />
                                    <div class="resultItemRight">
                                        <div class="movieName">{{item.title}}</div>
                                        <div class="classLabel" :class='"Class1"' v-if="item.rating == 5">Class E</div>
                                        <div class="classLabel" :class='"Class2"' v-else-if="item.rating == 4">Class D</div>
                                        <div class="classLabel" :class='"Class3"' v-else-if="item.rating == 3">Class C</div>
                                        <div class="classLabel" :class='"Class4"' v-else-if="item.rating == 2">Class B</div>
                                        <div class="classLabel" :class='"Class5"' v-else-if="item.rating == 1">Class A</div>
                                        <div class="movieDate flexStart">
