                        </div>
                    </template>
                </div>
                
            </div>
        </template>
        <van-popup v-model="showUpgreadAlert" class='upgreadAlertBox flexCenter' style="" v-if="configData">
            <img class='modelCloseIcon' src="../assets/images/modelCloseIcon.png" alt="" @click="showUpgreadAlert = false">
            <img class='upgreadIcon' src="../assets/images/upgreadIcon.png" alt="">
            <p class="upgreadText">This copyright requires<br>a higher VIP level to access</p>
            <div class="requiredText flexStart" style="margin-bottom: .06rem;">
                <span class='blueText'>Required level: </span>
                <img class='smallVipIcon' src="../assets/images/smallV0Icon.png" alt="" v-if="requireLevel == 0">
                <img class='smallVipIcon' src="../assets/images/smallV1Icon.png" alt="" v-else-if="requireLevel == 1">
                <img class='smallVipIcon' src="../assets/images/smallV2Icon.png" alt="" v-else-if="requireLevel == 2">
                <img class='smallVipIcon' src="../assets/images/smallV3Icon.png" alt="" v-else-if="requireLevel == 3">
                <img class='smallVipIcon' src="../assets/images/smallV4Icon.png" alt="" v-else-if="requireLevel == 4">
                <img class='smallVipIcon' src="../assets/images/smallV5Icon.png" alt="" v-else-if="requireLevel == 5">
                <span class='blueText'> {{'v'+requireLevel}}</span>
            </div>
            <div class="currentText flexStart">
                <span class='redText'>Current level: </span>
                <img class='smallVipIcon' src="../assets/images/smallV0Icon.png" alt="" v-if="configData.myLevel == 0">
                <img class='smallVipIcon' src="../assets/images/smallV1Icon.png" alt="" v-else-if="configData.myLevel == 1">
                <img class='smallVipIcon' src="../assets/images/smallV2Icon.png" alt="" v-else-if="configData.myLevel == 2">
                <img class='smallVipIcon' src="../assets/images/smallV3Icon.png" alt="" v-else-if="configData.myLevel == 3">
                <img class='smallVipIcon' src="../assets/images/smallV4Icon.png" alt="" v-else-if="configData.myLevel == 4">
                <img class='smallVipIcon' src="../assets/images/smallV5Icon.png" alt="" v-else-if="configData.myLevel == 5">
                <span class='redText'> {{'v'+configData.myLevel}}</span>
            </div>
            <div class="submitBtn_91 GradientAllowBtn" style="margin-top: .56rem;width: 100%;" @click="goPage('/VIP')">Upgrade Now</div>
        </van-popup>
        <van-popup v-model="showUpgreadAlert2" class='upgreadAlertBox flexCenter' style="" v-if="configData">
            <img class='modelCloseIcon' src="../assets/images/modelCloseIcon.png" alt="" @click="showUpgreadAlert2 = false">
            <img class='upgreadIcon' src="../assets/images/upgreadIcon.png" alt="">
            <p class="upgreadText" style="margin-bottom: 0;">Upgrade your membership level<br>to get full rewards</p>
            <div class="submitBtn_91 GradientAllowBtn" style="margin-top: .72rem;width: 100%;" @click="goPage('/VIP')">Upgrade Now</div>
            <p class="laterText" @click="showUpgreadAlert2 = false">Remind me later</p>
        </van-popup>
	</div>
</template>
<script>
	import https from '../api/https'
    import { FormDataShim} from '../assets/js/compress';
	export default {
		name: 'VideoCreation',
		data: function(){
			return {
				event: '',
				bgWrapHeight: 200,
				showSearchBox: false,
                creatSuccess: false,
                configData: null,
                coverUrl: '',
                selectGenresArr: [],
                genresData: [],
                intro: '',
                IntroAlertText: '',
                uploadStatus: 'noUpload',
                coverUploadSuccess: false,
                videoUploadSuccess: false,
                showUpgreadAlert: false,
                showUpgreadAlert2: false,
                requireLevel: '',


                copyRightName: '',
                copyrightId: '',
                postCoverId: '',
                postFileId: '',
                postDuration: '',
                postVideoWidth: '',
                postVideoHeight: '',


                searchList: [],
                searchWord: '',
                listLoading: false,
                finished: false,
                showResult: false,
                page: 1,
			}
		},
		created(){
			this.getData()

            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getData: function(){
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				https.fetchGet('/api/video/creation/config').then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.configData = res.data
                        for (let index = 0; index < this.configData.genres.length; index++) {
                            let obj = {
                                name: this.configData.genres[index],
                                isSelect: false,
                            }
                            this.genresData.push(obj)
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            postVideo(){
                if(!this.checkPosted) {
                    return
                }
                if(this.configData.remainingPost == 0) {
                    this.showUpgreadAlert2 = true
                    return
                }
                if(this.configData.isBlack) {
                    this.$toast({
                        message: 'Content violation detected',
                        forbidClick: true,
                        duration: 2000,
                        className: 'smallToast'
                    });
                    return
                }
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    file: this.postFileId,
                    cover: this.postCoverId,
                    duration: this.postDuration,
                    width: this.postVideoWidth,
                    height: this.postVideoHeight,
                    intro: this.intro,
                    genres: this.selectGenresArr.toString(),
                    copyrightId: this.copyrightId,
                }
				https.fetchPost('/api/video/creation/upload',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        this.creatSuccess = true

					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
            },
            uploadVideo() {
                // let that = this
				var filesId = document.getElementById('pop_video')
				var videoId = document.getElementById('videoId')
				var url = getFileURL(filesId.files[0]);
                this.uploadStatus = 'uploading'
				if(url) {
                    console.log(url)
					videoId.src = url
					setTimeout(() => {
						let duration = videoId.duration
						if(Math.floor(duration) > this.configData.videoLimit.timeLimit) {
                            this.$toast({
                                message: 'Video is too long',
                                forbidClick: true,
                                duration: 2000,
                                className: 'smallToast'
                            });
                            this.uploadStatus = 'uploadFailed'
                            return
						}else if(filesId.files[0].size > this.configData.videoLimit.sizeLimit*1024*1024) {
                            this.$toast({
                                message: 'File size is too large',
                                forbidClick: true,
                                duration: 2000,
                                className: 'smallToast'
                            });
                            this.uploadStatus = 'uploadFailed'
                            return
						}
                        // else if(videoId.videoWidth < 480) {
                        //     this.$toast({
                        //         message: 'Video definition is too low',
                        //         forbidClick: true,
                        //         duration: 2000,
                        //         className: 'smallToast'
                        //     });
                        //     this.uploadStatus = 'uploadFailed'
                        //     return
                        // }
                        else if(videoId.videoWidth/videoId.videoHeight > 3 || videoId.videoHeight/videoId.videoWidth > 3) {
                            this.$toast({
                                message: 'Video aspect ratio is out of range',
                                forbidClick: true,
                                duration: 2000,
                                className: 'smallToast'
                            });
                            this.uploadStatus = 'uploadFailed'
                            return
                        }
                        else {
                            this.uploadStatus = 'uploading'

                            this.postDuration = duration.toFixed(8)
                            this.postVideoWidth = videoId.videoWidth
                            this.postVideoHeight = videoId.videoHeight
                            videoId.currentTime = 1 // 
                            if(videoId.videoWidth > 716 || videoId.videoHeight > 482 ) {
                                if(videoId.videoWidth/videoId.videoHeight > 716/482) {
                                    videoId.width = 716
                                    videoId.height = 716/((videoId.videoWidth/videoId.videoHeight))
                                }else if(videoId.videoWidth/videoId.videoHeight < 716/482){
                                    videoId.height = 482
                                    videoId.width = (videoId.videoWidth/videoId.videoHeight)*482
                                }
                            }else {
                                videoId.width = videoId.videoWidth
                                videoId.height = videoId.videoHeight
                            }
                            //canvas
                            const canvas = document.createElement("canvas")
                            canvas.width = videoId.width
                            canvas.height = videoId.height
                            this.$nextTick(()=>{
                                // canvas
                                canvas.getContext("2d").drawImage(videoId, 0, 0, canvas.width, canvas.height);
                                // base64
                                const img = canvas.toDataURL("image/jpeg") // base64
                                this.coverUrl = img
                                console.log(img)
                                var form = this.getFormData();
                                // filesId.files[0].type = 'video/mp4'
                                form.append('file', filesId.files[0]);
                                form.append('name', filesId.files[0].name.split('.')[0]+'.mp4');
                                form.append('resType', 'vfile');
                                console.log('form-file',form.get('file'))
                                this.uploadFile2(form)
                                this.dataURLtoFile(this.coverUrl,filesId.files[0].name,'vcover')
                            })
                        }
					}, 1000);
				}
				function getFileURL(file){
					let getUrl = null
					if(window.createObjectURL != undefined) {
						getUrl = window.createObjectURL(file);
					}else if(window.URL != undefined) {
						getUrl = window.URL.createObjectURL(file);
					}else if(window.webkitURL != undefined) {
						getUrl = window.webkitURL.createObjectURL(file);
					}
					return getUrl;
				}
				
			},
            dataURLtoFile(base64Data,name,resType) {//base64
                var arr = base64Data.split(','), mime = arr[0].match(/:(.*?);/)[1],
                bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
                while(n--){
                    u8arr[n] = bstr.charCodeAt(n);
                }
                let blob = new File([u8arr],name,{type:mime});
                var form2 = this.getFormData();
                form2.append('file', blob);
                form2.append('name', name.split('.')[0]+'.jpeg');
                form2.append('resType', resType);
                this.uploadFile2(form2)
            },
            uploadFile2(form){
                let that = this
                var xhr = new XMLHttpRequest();
                xhr.open('post', https.ImgUploadUrl);
                xhr.setRequestHeader("user-token", localStorage.getItem('user-token'));
                xhr.setRequestHeader("Data-Type", 'json');
                xhr.withCredentials = false;
                xhr.send(form);
                xhr.onreadystatechange = function() {
                    console.log(xhr.readyState)
                    if (xhr.readyState == 4 && xhr.status == 200) {
                        let res = JSON.parse(xhr.response)
                        console.log(res)
                        console.log(form.get('name'))
                        if(res.code == 0) {
                            let uploadType = form.get('name').split('.')[1]
                            if(uploadType == 'jpeg') {
                                that.postCoverId = res.data.resId
                                that.coverUploadSuccess = true
                                if(that.videoUploadSuccess == true) {
                                    that.uploadStatus = 'uploadComplate'
                                }
                            }else {
                                that.postFileId = res.data.resId
                                that.videoUploadSuccess = true
                                if(that.coverUploadSuccess == true) {
                                    that.uploadStatus = 'uploadComplate'
                                }
                            }
                        }else {
                            that.$toast({
                                message: res.msg,
                                forbidClick: true,
                                icon: require('../assets/images/errorIcon.png'),
                                duration: 2000,
                                className: 'resultToast'
                            });
                            that.uploadStatus = 'uploadFailed'
                        }
                    }
                    setTimeout(() => {
                        if (xhr.readyState != 4 || xhr.status != 200) {
                            that.$toast({
                                message: https.NetworkErrorText,
                                forbidClick: true,
                                icon: require('../assets/images/errorIcon.png'),
                                duration: 2000,
                                className: 'resultToast'
                            });
                            that.uploadStatus = 'uploadFailed'
                        }
                    }, 30*1000);
                }
            },
            getFormData() {
                var isNeedShim = ~navigator.userAgent.indexOf('Android')
                    && ~navigator.vendor.indexOf('Google')
                    && !~navigator.userAgent.indexOf('Chrome')
                    && navigator.userAgent.match(/AppleWebKit\/(\d+)/).pop() <= 534;
                return isNeedShim ? new FormDataShim() : new FormData()
            },
			showSearch(){
                this.showSearchBox = true
                this.$nextTick(()=>{   //
                    this.$refs.searchInput.focus();
                })
            },
            selectGenres(item,index2){
                var index = this.selectGenresArr.indexOf(item.name); 
                if (index > -1) { 
                    this.selectGenresArr.splice(index, 1); 
                    this.genresData[index2].isSelect = false
                } else {
                    if(this.selectGenresArr.length < 3) {
                        this.selectGenresArr.push(item.name)
                        this.genresData[index2].isSelect = true
                    }else {
                        this.$toast({
							message: 'Too many genres',
							forbidClick: true,
							duration: 2000,
							className: 'smallToast'
						});
                    }
                }
                console.log(this.selectGenresArr)

            },
            inputIntro(intro) {
                let rge = /[<'>\n]/g;
				if(!intro || intro.length < 20) {
					this.IntroAlertText = 'At least 20 characters'
				}else if(rge.test(intro)){
					this.IntroAlertText = 'Cannot contain special characters'
				}else {
					this.showIntroAlert = false
					this.IntroAlertText = ''
				}
			},
            clickSearch(){
                this.page = 1
                this.getSearchData()
            },
            getSearchData: function(){
                this.showLoading = true
                setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    word: this.searchWord,
                    page: this.page,
                }
				https.fetchGet('/api/library/search',params).then((data) => {
                        this.showLoading = false
                        this.$toast.clear()
                        this.showResult = true
                        let res = data.data
					if(res.code == 0) {
                        
						this.searchList = res.data.list
                        this.listLoading = false
                        this.page++
                        if(!res.data.hasNext) {
                            this.finished = true
                            console.log('finished')
                        }
                        
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getSearchData()
					}
				})
			},
            selectMovie(item){
                if(this.configData.myLevel/1 < item.creation.requireLevel/1) {
                    this.requireLevel = item.creation.requireLevel
                    this.showUpgreadAlert = true
                }else {
                    this.copyrightId = item.id
                    this.copyRightName = item.title
                    this.searchList = []
                    this.searchWord = ''
                    this.showSearchBox = false
                    this.showResult = false
                }
            },
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		},
        computed:{
            checkPosted: function(){
                if(this.videoUploadSuccess && this.coverUploadSuccess && this.intro != '' && this.IntroAlertText == '' && this.selectGenresArr.length > 0) {
                    if(this.configData.requireCopyRight) {
                        if(this.copyrightId != '') {
                            return true
                        }else {
                            return false
                        }
                    }else {
                        return true
                    }
                }else {
                    return false
                }
            },
        }
	}
</script>
